import { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import {
    AppBar,
    Box,
    Typography,
    Card,
    CardHeader,
    CardContent,
    CardActions,
    Button,
    Toolbar,
    IconButton,
} from "@mui/material";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { getStorageData } from "../functions/functions";

export function ListAuditoriaDisponiblePage() {
    const [auditorias, setAuditorias] = useState([]);
    const navigate = useNavigate();

    useEffect(() => {
        document.title = "Auditorias disponibles";
        const storageData = getStorageData();
        setAuditorias(storageData.auditorias);
    }, []);

    return (
        <Box sx={{ flexGrow: 1 }}>
            <AppBar position="fixed">
                <Toolbar
                    variant="dense"
                    sx={{
                        bgcolor: "#59185E",
                    }}
                >
                    <IconButton
                        edge="start"
                        size="large"
                        color="inherit"
                        aria-label="volver"
                        onClick={() => navigate(-1)}
                        sx={{ mr: 2 }}
                    >
                        <ArrowBackIcon />
                    </IconButton>
                    <Typography
                        variant="h6"
                        component="div"
                        sx={{ flexGrow: 1 }}
                    >
                        Auditorias disponibles
                    </Typography>
                </Toolbar>
            </AppBar>
            <Box
                sx={{
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                    alignItems: "center",
                    pt: 8,
                    px: 1,
                    pb: 2,
                    bgcolor: "#ecf0f1",
                }}
            >
                {auditorias.map((auditoria) => (
                    <Card
                        key={auditoria.id}
                        raised={true}
                        sx={{ mb: 2, width: "98%", overflow: "visible" }}
                    >
                        <CardHeader
                            title={auditoria.nombre}
                            subheader={`${auditoria.tipo} ${
                                auditoria.categoria_id
                                    ? auditoria.categoria
                                    : ""
                            }`}
                        />
                        <CardContent>
                            <em> {auditoria.descripcion} </em>
                        </CardContent>
                        <CardActions
                            sx={{
                                display: "flex",
                                justifyContent: "flex-end",
                            }}
                        >
                            <Link to={`/auditoria/aplica/${auditoria.id}`}>
                                <Button
                                    size="small"
                                    variant="contained"
                                    sx={{
                                        bgcolor: "#59185E",
                                    }}
                                >
                                    Aplicar
                                </Button>
                            </Link>
                        </CardActions>
                    </Card>
                ))}
            </Box>
        </Box>
    );
}
